import TextError from "../TextError"
import {  useField, ErrorMessage } from "formik";


export default function InputBlockSelect (props){

    const [field] = useField(props)
    const {arrOfValue, text, name, stylename} = props
    
return (
  <>
  <div className="input-wrap">
      <label htmlFor={name}>
      {text}
        <ErrorMessage name={name} component={TextError} />
      </label>
    <select className={stylename} {...props} {...field} >
    {arrOfValue.map((value, index)=>{
     
        if(index === 0){
          return (
            <option key={value} value=''>
            {value}
            </option>
          )
        } else{
          return(
        <option key={value} value={value}>
          {value}
        </option>
          )
        }
      
    })}
      </select>
      </div>
    
  </>
);
}
