import { useField, useFormikContext } from "formik"
import { ErrorMessage } from "formik"
import TextError from "../TextError"
import React from "react"



export default function MyField (props){
  const {
    values: {ShipmentDescriptionCode },
    touched,
    setFieldValue} = useFormikContext()
    const [field] = useField(props)
    const {text, name} = props
React.useEffect(()=> {
if(
  ShipmentDescriptionCode.trim() === "P"
){
  setFieldValue('TotalShipmentDescriptionCode', 'T')
} else {
  setFieldValue('TotalShipmentDescriptionCode', '')
  setFieldValue('TotalNumberofPieces', '')
}
}, [ touched.ShipmentDescriptionCode, ShipmentDescriptionCode, setFieldValue  ])


return (
  <div className="input-wrap">
  <label htmlFor={name}>
    {text}
  </label>
  <ErrorMessage name={name} component={TextError}/>
  < input {...props} {...field} disabled={true}/>
  </div>
)
}